import React, { Component } from 'react'
import './css.css';
import Web3 from 'web3';
import ShopToken from '../ShopToken/ShopToken'
import FreeToken from '../FreeToken/FreeToken'
import ShopPair from '../ShopPair/ShopPair'

class Favorites extends Component {
constructor(){
  super()
  this.state={favoriteProducts:[]}
}

    componentDidMount(){
        this.loadFaves()
    }

async loadFaves(){
    
        
    
    let final=[]
    let s=this.getCookie("favorite")
    let arr=s.split(",")
    let prp=this.props
    let stt=this.state
    await window.ethereum.enable().then(async function (accounts) { 
     
    for(let i=0;i<arr.length;i++){
      
      if(arr[i]!=""){
        let token=arr[i].substr(0,arr[i].indexOf("_"))
        
        let list=arr[i].substr(arr[i].indexOf("_")+1,42)
        
        let index=arr[i].substr(arr[i].lastIndexOf("x")+1,arr[i].length)
        const { REACT_APP_TICKETABI } = process.env;
        const tickC = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), list) 
        let priceList = await tickC.methods.prices().call()
        let code = await tickC.methods.code().call()
        const { REACT_APP_PriceListABI } = process.env;
        const priceListC = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceList) 
        let price = await priceListC.methods.price(token).call()
        if(code==4)final.push(<ShopToken db={prp.db} priceList={list} address={token} price={price} index={list+"x"+index} ticket={list} key={i} edit={""} name={"nnn"}></ShopToken>);
        if(code==7)final.push(<FreeToken   db={prp.db} priceList={list} address={token} price={price} index={list+"x"+index} ticket={list} key={i} edit={""} name={"nnn"}></FreeToken>);
        if(code==9)final.push(<ShopPair  db={prp.db} priceList={list} address={token} price={price} index={list+"x"+index} ticket={list} key={i} edit={""} name={"nnn"}></ShopPair>);
 
      }
        let s=arr[i].substr(0,arr[i].indexOf("_"))
        var divt = document.createElement("div");
        divt.innerHTML = s;
       divt.style.margin="auto"
       divt.style.color="white"
       divt.style.width="500px"
       divt.style.fontSize="20px"
       divt.style.marginBottom="20px"
        //document.getElementById("faves_panel").appendChild(divt)
    }

  })
  this.setState({favoriteProducts:final})
}

    deleteFavorites(){
       
        document.cookie = "favorite=;"; 
        this.loadFaves()
    }
    setCookie(cname, cvalue) {
        let s=this.getCookie(cname)
        let arr=s.split(",")
        arr.push(cvalue)
    
        document.cookie = cname + "=" + arr.toString() + ";";
        
    }
    
    getCookie(cname) {
        var name = cname + "=";
        var decodedCookie = decodeURIComponent(document.cookie);
        var ca = decodedCookie.split(';');
        for(var i = 0; i <ca.length; i++) {
          var c = ca[i];
          while (c.charAt(0) == ' ') {
            c = c.substring(1);
          }
          if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
          }
        }
        return "";
      }
   
    render(){
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="faves_flyer"><div id="favorites_mainlabel">favorites</div>
            <div id="favorites_image"  alt="favorites">⭐️</div>
            <div id="favorites_sublabel">favorite products list</div>
       
      <div id="faves_panel"></div>
        <div>{this.state.favoriteProducts}</div>
      <input className="delete_button" type="button" value="DELETE ALL FAVORITES" onClick={() => this.deleteFavorites()}></input>
      </div>
        </div>
        );
    }


}


const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Favorites